import axios from "axios";
import { useState, useEffect } from "react";
import { useParams, useHistory } from "react-router";

import Button from "@mui/material/Button";
import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer,
} from "recharts";

import css from "./styles.module.css";

const LabelY = () => {
  return (
    <text
    transform='rotate(90) translate(295 115)'
      offset="5"
      x="50"
      y="235"
      class="recharts-text recharts-label"
      text-anchor="middle"
    >
      <tspan x="50" dy="0.355em">
          %
      </tspan>
    </text>
  );
};

export const UserInfo = () => {
  const { id } = useParams();
  const history = useHistory();

  const [data, setData] = useState([]);
  const [author, setAuthor] = useState("");

  async function getChartInfo() {
    const req = await axios(`https://api.artydev.ru/api/evergreen/${id}`);
    const newData = req.data.data.map((item) => ({
      name: String(item.parse_dt),
      year_relative_yield: Number(item.year_relative_yield),
      amt: Number(item.month_operations_count),
    }));
    setData(newData.reverse());
    setAuthor(req.data.author);
  }

  useEffect(() => {
    getChartInfo();
  }, []);

  return (
    <div className={css.wrapper}>
      <Button onClick={() => history.push("/")}> </Button>
      <h3>{author}</h3>
      <div className={css.chartWrap}>
        <LineChart
          width={900}
          height={500}
          syncId="anyId"
          data={data}
          margin={{
            top: 5,
            right: 30,
            left: 20,
            bottom: 5,
          }}
        >
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey="name" />
          <YAxis
            dataKey="year_relative_yield"
            type="number"
            label={<LabelY />}
            className={css.yAx}
          />
          <Tooltip label="Ghbdtn" />
          <Line
            type="natural"
            dataKey="year_relative_yield"
            stroke="#82ca9d"
            activeDot={{ r: 6 }}
          />
        </LineChart>
      </div>
    </div>
  );
};
